@import '../../../styles/shared';

.project-detail-admin-section {
    margin-top: 0.2rem;
    @include flexCenterDir(row);
}

.project-detail-info-section {
    .project-detail-title {
        color: white;
        background-color: $primaryColor;
        padding: 1rem 1rem 1rem 1rem;
        margin: 2rem 1rem;
        font-weight: bold;
        font-size: 2rem;
        border-radius: 5px;
    }
    .project-detail-desc {
        margin: 1.5rem 1rem 2.5rem 1rem;
        font-size: 1.5rem;
        color: $primaryColor;
        text-align: center;
    }
}

.donatables-na-co-chci-darovat {
    margin-bottom: 2rem;
}




.project-detail-main-img--wrapper {
    max-width: 45rem;
    @include flexCenter();
    // height: 20rem;
}

.project-detail-main-img {
    width: 100%;
    // height: 100%;
    display: block;
}